import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import SearchBar from './components/SearchBar';
import Player from './components/Player';
import { Container, Typography, List, ListItem, ListItemText, CircularProgress, Snackbar, Alert, Paper, Box, useTheme, useMediaQuery } from '@mui/material';
import { useLocation, useNavigate } from 'react-router-dom';
import { IconButton } from '@mui/material';
import { Brightness4, Brightness7 } from '@mui/icons-material';
import { ThemeContext } from './context/ThemeContext';
import { encrypt, decrypt } from './utils/crypto';

function App() {
  const [videos, setVideos] = useState([]);
  const [currentVideo, setCurrentVideo] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [volume, setVolume] = useState(100);
  const location = useLocation();
  const navigate = useNavigate();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    // Get video ID from URL parameters
    const params = new URLSearchParams(location.search);
    const videoId = params.get('v');
    if (videoId) {
      setCurrentVideo(videoId);
    }
  }, [location]);

  const handleVideoSelect = (videoId) => {
    setCurrentVideo(videoId);
    // Update URL with video ID
    navigate(`?v=${videoId}`);
  };

  const handleSearch = async (searchTerm) => {
    setLoading(true);
    setError(null);
    try {
      const API_KEY = 'AIzaSyCucLgxpUROxLbBc20tKWsboBrj0a_0N7o';
      
      const response = await axios.get(`https://www.googleapis.com/youtube/v3/search`, {
        params: {
          part: 'snippet',
          maxResults: 10,
          key: API_KEY,
          q: searchTerm,
          type: 'video',
          videoCategoryId: '10' // Add music category filter
        },
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Origin': 'https://eldego07.github.io',
          'Referer': 'https://eldego07.github.io/elsound-app/'
        },
        withCredentials: false
      });
      
      if (!response.data || !response.data.items) {
        throw new Error('Invalid response from YouTube API');
      }
      
      console.log('API Response:', response.data); // Debug line
      setVideos(response.data.items);
    } catch (error) {
      console.error('Search error details:', {
        message: error.message,
        response: error.response?.data,
        status: error.response?.status
      });
      setError(`Failed to fetch videos: ${error.response?.data?.error?.message || error.message}`);
    } finally {
      setLoading(false);
    }
  };

  const handleVolumeChange = (newValue) => {
    setVolume(newValue);
  };

  const { darkMode, setDarkMode } = useContext(ThemeContext);

  const handleTitleClick = () => {
    navigate('/');
    setCurrentVideo(null);
    setVideos([]);
  };

  return (
    <Container maxWidth="lg" sx={{ 
      py: 4, 
      position: 'relative',
      backgroundColor: 'transparent',
      minHeight: '100vh'
    }}>
      <Paper elevation={3} sx={{ 
        p: 3, 
        mb: 4, 
        borderRadius: '16px', 
        maxWidth: '800px',
        margin: '0 auto',
        background: darkMode 
          ? 'linear-gradient(145deg, rgba(26,26,26,0.9), rgba(45,45,45,0.9))'
          : 'linear-gradient(145deg, rgba(240,240,240,0.9), rgba(255,255,255,0.9))',
        backdropFilter: 'blur(10px)',
        border: '1px solid',
        borderColor: darkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)',
      }}>
        <Box sx={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          maxWidth: '100%'  // Add this line
        }}>
          <Typography 
            variant="h3" 
            className="app-title"
            onClick={handleTitleClick}
            sx={{ 
              margin: '20px 0',
              fontWeight: 'bold',
              textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
              cursor: 'pointer',
              '&:hover': {
                opacity: 0.8
              }
            }}
          >
            ElSound
          </Typography>
          <IconButton 
            onClick={() => setDarkMode(!darkMode)} 
            sx={{
              backgroundColor: 'rgba(255, 255, 255, 0.1)',
              '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.2)'
              }
            }}
          >
            {darkMode ? <Brightness7 /> : <Brightness4 />}
          </IconButton>
        </Box>
        <SearchBar onSearch={handleSearch} />
      </Paper>

      <Box sx={{ 
        display: 'flex', 
        flexDirection: isMobile ? 'column' : 'row', 
        gap: 3,
        minHeight: 'calc(100vh - 200px)'
      }}>
        <Box sx={{ 
          flex: 2,
          display: 'flex',
          flexDirection: 'column'
        }}>
          <div className="youtube-player-container">
            <div className="player-header">
              <span className="now-playing">Now Playing</span>
            </div>
            <Player 
              videoId={currentVideo} 
              volume={volume}
              onVolumeChange={setVolume}
            />
          </div>
        </Box>

        <Box sx={{ flex: 1 }}>
          {loading ? (
            <Box display="flex" justifyContent="center" m={2}>
              <CircularProgress sx={{ color: darkMode ? '#fff' : '#1976d2' }} />
            </Box>
          ) : (
            videos.length > 0 && (
              <div className="playlist-container">
                {videos.map((video) => (
                  <div 
                    key={video.id.videoId}
                    className="playlist-item"
                    onClick={() => handleVideoSelect(video.id.videoId)}
                  >
                    <Typography variant="subtitle1" sx={{ color: darkMode ? '#fff' : '#000' }}>
                      {video.snippet.title}
                    </Typography>
                    <Typography variant="caption" sx={{ color: darkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)' }}>
                      {video.snippet.channelTitle}
                    </Typography>
                  </div>
                ))}
              </div>
            )
          )}
        </Box>
      </Box>

      <Typography
        variant="body2"
        sx={{
          position: 'fixed',
          bottom: 16,
          right: 16,
          padding: '8px 12px',
          borderRadius: '4px',
          backgroundColor: darkMode ? 'rgba(0, 0, 0, 0.7)' : 'rgba(255, 255, 255, 0.9)',
          color: darkMode ? '#ffffff' : '#000000',
          fontStyle: 'italic',
          fontWeight: 'medium',
          zIndex: 9999,
          backdropFilter: 'blur(5px)',
          textShadow: darkMode ? '1px 1px 2px rgba(0,0,0,0.5)' : '1px 1px 2px rgba(255,255,255,0.5)',
          border: '1px solid',
          borderColor: darkMode ? 'rgba(255, 255, 255, 0.3)' : 'rgba(0, 0, 0, 0.2)',
          boxShadow: '0 2px 8px rgba(0,0,0,0.2)',
          transition: 'all 0.3s ease',
          '&:hover': {
            backgroundColor: darkMode ? 'rgba(0, 0, 0, 0.8)' : 'rgba(255, 255, 255, 1)',
            transform: 'translateY(-2px)',
          }
        }}
      >
        di produzione Casolaro Diego
      </Typography>

      <Snackbar 
        open={!!error} 
        autoHideDuration={6000} 
        onClose={() => setError(null)}
      >
        <Alert onClose={() => setError(null)} severity="error" sx={{ 
          width: '100%',
          color: '#fff',
          backgroundColor: 'rgba(211, 47, 47, 0.9)'
        }}>
          {error}
        </Alert>
      </Snackbar>
    </Container>
  );
}

export default App;